import { useState } from "react";
import styled from "styled-components";
import AngleDown from "../assets/SVG/angle-down-solid.svg";
import ProjectsList from "./ProjectsList";

const ShowMoreDetails = ({ challenge, index }) => {
	const [showMore, setShowMore] = useState(false);
	console.log(challenge);
	return (
		<>
			<ChallengeWrapper key={index} onClick={() => setShowMore((prevState) => !prevState)}>
				{challenge.name}
				<AngleIcon src={AngleDown} showMore={showMore} />
			</ChallengeWrapper>
			{showMore === true ? challenge.details : null}
		</>
	);
};

export default ShowMoreDetails;

const ChallengeWrapper = styled.div`
	display: flex;
	cursor: pointer;
`;

const AngleIcon = styled.img`
	width: 10px;
	transition: all 0.15s ease-in-out;
	margin-left: 8px;
	transform: ${(props) => (props.showMore ? "scaleY(-1)" : "scaleY(1)")};
`;
