import { useState } from "react";
import styled from "styled-components";
import Devices from "./Devices";
import LoadingSpinner from "../assets/SVG/spinner-solid.svg";

const Contact = () => {
	const [buttonText, setButtonText] = useState("Send Enquiry");
	const [loading, setLoading] = useState(false);
	const [formSubmitted, setFormSubmitted] = useState(false);
	const [email, setEmail] = useState()
	const [name, setName] = useState()

	const handleClick = () => {
		setButtonText("Waiting");
		setLoading(true);
	};

	const handleSubmit = (e) => {
		e.preventDefault();
		const data = new URLSearchParams();
		setName(document.getElementById("firstname").value)
		for (const pair of new FormData(document.getElementById("form"))) {
			data.append(pair[0], pair[1]);
		}

		fetch("https://formsubmit.co/2a2226c91720e70ad34fab6df208b994", {
			method: "post",
			body: data,
		}).then(() => {
			setButtonText("Success");
			setLoading(false);
			setFormSubmitted(true);
			setEmail(document.getElementById("email").value);
	
		});
	};

	return (
		<MainWrapper id='section-contact'>
			<SubWrapper>
				<SectionHeading>Contact</SectionHeading>
				<p>For enquiries, please fill out the form below and I will be in touch!</p>
			
					<ContactForm id='form' onSubmit={handleSubmit}>
						{formSubmitted ? (
							<ThanksWrapper>							
								<h4>Thank you for your enquiry, {name}!</h4><br />
								<h4>I will be in touch shortly.</h4>
							</ThanksWrapper>
						) : (
							<InputWrapper>
								<InputSubWrapper>
									<input type='hidden' name='_captcha' value='false'></input>
									<input type='hidden' name='_next' value='https://jbuck44.github.io/jb-dev/'></input>
									<Input type='text' name='firstname' id='firstname' placeholder='First Name' required></Input>
									<Input type='text' name='surname' id='surname' placeholder='Surname' required></Input>
								</InputSubWrapper>
								<Input type='email' name='email' id='email' placeholder='Email Address' required></Input>
								<TextArea type='text' name='message' id='message' placeholder='Your message' required></TextArea>
								<InputSubWrapper justify={"flex-end"}>
									<Button type='submit' onClick={handleClick}>
										{buttonText}
										<LoadingIcon src={LoadingSpinner} loading={loading}></LoadingIcon>
									</Button>
								</InputSubWrapper>
							</InputWrapper>
						)}{" "}
					</ContactForm>
			
			</SubWrapper>
		</MainWrapper>
	);
};

export default Contact;

const MainWrapper = styled.div`
	height: auto;
	width: 2000px;
	margin: 0 auto;
	display: flex;
	flex-direction: row;
	justify-content: center;
	padding:2rem 0;

	@media ${Devices.tablet} {
		flex-direction: column;
		gap: 3rem;
		width: 100%;
		height: auto;
		padding: 1.5rem 0;
	}
	@media ${Devices.mobile} {
		gap: 0.5rem;
	}
`;

const SectionHeading = styled.h2`
	border-bottom: 1px solid grey;
`;
const SubWrapper = styled.div`
	width: 2000px;
	margin: 0 auto;
	@media ${Devices.tablet} {
		width: 100%;
		padding: 0 1rem;
	}
	@media ${Devices.mobile} {
	}
`;

const ContactForm = styled.form`

`

const ThanksWrapper = styled.div`
	border: 1px solid #6f7378;
	border-radius: 4px;
	padding: 0.5rem;
	height: 300px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items:center;
	text-align: center;
`;

const InputWrapper = styled.div`
	display: flex;
	flex-direction: column;
	gap: 0.5rem;
	@media ${Devices.tablet} {
	}
	@media ${Devices.mobile} {
	}
`;

const InputSubWrapper = styled.div`
	display: flex;
	gap: 0.5rem;
	justify-content: ${(props) => props.justify};
	@media ${Devices.tablet} {
	}
	@media ${Devices.mobile} {
	}
`;

const Input = styled.input`
	border: 1px solid #6f7378;
	border-radius: 4px;
	width: 100%;
	height: 60px;
	padding: 0.5rem;
	@media ${Devices.tablet} {
	}
	@media ${Devices.mobile} {
	}
`;

const TextArea = styled.textarea`
	border: 1px solid #6f7378;
	border-radius: 4px;
	height: 120px;
	padding: 0.5rem;
	@media ${Devices.tablet} {
	}
	@media ${Devices.mobile} {
	}
`;

const Button = styled.button`
	width: 25%;
	height: 80px;
	border: 1px solid #6f7378;
	background-color: #6f7378;
	color: white;
	font-weight: 600;
	font-size: 1.5rem;
	border-radius: 4px;
	transition: all 0.15s ease;
	display: flex;
	justify-content: center;
	align-items: center;
	&:hover {
		background-color: #cacaca;
	}
	@media ${Devices.tablet} {
		width: 100%;
		height: 60px;
		font-size: 1.1rem;
	}
	@media ${Devices.mobile} {
		font-size: 1.1rem;
	}
`;

const LoadingIcon = styled.img`
	width: 20px;
	margin-left: 10px;
	opacity: ${(props) => (props.loading ? "1" : "0")};
	animation: ${(props) => (props.loading ? "spin 3s linear infinite" : null)};
`;
