import styled from "styled-components";
import Devices from "./Devices";

const About = () => {
	return (
		<MainWrapper id="section-about">
			<SubWrapper>
				<SectionHeading>About</SectionHeading>
				<div className='flex flex-col'>
					<h3>Early career</h3>
					<p>
						I graduated from the University of South Wales in 2013 with a Bsc in Aircraft Maintenance engineering. However, it was clear to me that a career in Aircraft Maintenance wasn't for me. Subsequently I had an offer from Jaguar Land Rover to work in their Aerodynamics and Vehicle Soiling team supporting physical testing and carrying out
						computational analysis using Matlab. Here I started to learn my first programming language as it was neccessary to perform some minor de-bugging tasks with the main scripts that we used as a team. I also taught myself Microsoft Excel VBA in my spare time so that I could write custom scripts to speed up the analysis and reporting
						procedures of certain tests within the department. This was of great benefit to the team and was something that I really enjoyed doing!
					</p>
					<p>Having worked in Mechanical Engineering for 5+ years and completed many engineering and problem solving related courses (mainly Greenbelt 6-sigma), I believe that I have developed a very good ability to solve complex problems. This is something in particular that I have found very useful in my leap into programming!</p>
					<h3>Moving Forward</h3>
					<p>
						During my time at JLR and particularly over the last 3 years since leaving JLR, I have spent time buying and renovating rental properties. Upon leaving JLR, my plan was to have 2 or 3 years away from Mechanical Engineering to focus on building my personal rental portfolio and to have a short career break. I am now seeking to resume my
						career but now pursuing my passion of Web Development rather than engineering.
					</p>
				</div>
			</SubWrapper>
		</MainWrapper>
	);
};

export default About;

const MainWrapper = styled.div`
	margin: 0 auto;
	display: flex;
	flex-direction: row;
	justify-content: center;
	background-color: #f9f9f9;
	padding:2rem 0;
	@media ${Devices.tablet} {
		flex-direction: column;
		gap: 3rem;
		width: 100%;
		height: auto;
		padding: 1.5rem 0;
	}
	@media ${Devices.mobile} {
		gap: 0.5rem;
	}
`;
const SubWrapper = styled.div`
	width: 2000px;
	margin: 0 auto;
	@media ${Devices.tablet} {
		width: 100%;
		padding: 0 1rem;
	}
	@media ${Devices.mobile} {
	}
`;

const SectionHeading = styled.h2`
	border-bottom: 1px solid grey;
`;
