import { useEffect, useRef, useState } from "react";
import styled from "styled-components";
import ProjectsList from "./ProjectsList";
import ArrowUpRight from "../assets/SVG/arrow-up-right-from-square-solid.svg";
import CloseMark from "../assets/SVG/xmark-solid.svg";
import Devices from "./Devices";
import ShowMoreDetails from "./ShowMoreDetails";

const ProjectDetails = ({ id, index }) => {
	const [mainPhoto, setMainPhoto] = useState();
	const [mainIndex, setMainIndex] = useState(0);

	const myRef = useRef(null);

	const importAll = () => {
		const r = require.context(`../assets/screenshots/`, true, /\.(png|jpe?g|svg)$/);
		let keys = [];
		r.keys().forEach((key) => {
			if (key.includes(id)) {
				keys.push(key);
			}
		});

		return keys.map(r);
	};
	const images = importAll();

	const GetIcons = () => {
		return ProjectsList[id].toolsUsed.map((tool, index) => (
			<Icon key={index}>
				<img src={tool.src} id={tool.id} />
			</Icon>
		));
	};

	useEffect(() => {
		setMainPhoto(images[0]);
	}, [id]);

	const PhotoThumnails = () => {
		const photoArray = [];

		images.map((image, index) => {
			return photoArray.push(
				<Thumbnail
					src={image}
					id={index}
					key={index}
					border={mainIndex === index ? "3px solid #6f7378" : "2px dotted #6f7378"}
					onClick={() => {
						setMainPhoto(image);
						setMainIndex(index);
					}}
				/>
			);
		});
		return photoArray;
	};

	const MainPhoto = () => {
		return <Main key={id} src={mainPhoto} />;
	};

	return (
		<>
			<MainWrapper name={id} id={`section-${id}`} ref={myRef} index={index}>
				<SubWrapper>
					<WebsiteLabel>{ProjectsList[id].label}</WebsiteLabel>
				
					<h3>{ProjectsList[id].name}</h3>

					<IconWrapper>
						<GetIcons />
					</IconWrapper>
					<h4>Description</h4>
					<p>{ProjectsList[id].description}</p>
					{ProjectsList[id].challenges && <h4>Techical Challenges</h4>}
					{ProjectsList[id].challenges &&
						ProjectsList[id].challenges.map((challenge, index) => (
							<ShowMoreDetails challenge={challenge} index={index} key={index} />
						))}
					<ButtonWrapper>
						<div className='relative'>
							{ProjectsList[id].id === "webscraper" ? null : (
								<a href={ProjectsList[id].github} target='_blank'>
									<Button>
										View Code on Github
										<img className='w-4' src={ArrowUpRight} />
									</Button>
								</a>
							)}
						</div>
						<div className='relative'>
							{ProjectsList[id].id === "webscraper" ? null : (
								<a href={ProjectsList[id].website} target='_blank'>
									<Button>
										View Website <img className='w-4' src={ArrowUpRight} />
									</Button>
								</a>
							)}
						</div>
					</ButtonWrapper>
				</SubWrapper>
				<PhotoWrapper>
					<MainPhoto />
					<ThumbnailWrapper>
						<PhotoThumnails />
					</ThumbnailWrapper>
				</PhotoWrapper>
			</MainWrapper>
		</>
	);
};

export default ProjectDetails;

const MainWrapper = styled.div`
	display: flex;
	border-bottom: ${(props) => (props.index === 4 ? null : "1px solid grey")};
	padding: 2rem 0;
	@media ${Devices.tablet} {
		flex-direction: column;
		width: 100%;
		padding: 1.5rem 0;
	}
	@media ${Devices.mobile} {
		gap: 0.5rem;
		padding: 1rem 0;
	}
`;

const SubWrapper = styled.div`
	width: 50%;
	@media ${Devices.tablet} {
		width: 100%;
	}
	@media ${Devices.mobile} {
	}
`;

const WebsiteLabel = styled.p`
	font-style: italic;
	padding: 0;
	margin: 0;
`;

const IconWrapper = styled.div`
	display: flex;
	align-items: center;
	margin: 0.5rem 0;
	gap: 8px;
	@media ${Devices.tablet} {
		margin: 1.5rem 0;
		gap: 5px;
	}
	@media ${Devices.mobile} {
		margin: 1rem 0;
		gap: 2px;
	}
`;

const ButtonWrapper = styled.div`
	display: flex;
	gap: 1rem;
	justify-content: center;
	margin: 1rem;
	@media ${Devices.tablet} {
		margin: 1.5rem;
	}
	@media ${Devices.mobile} {
		margin: 1rem;
	}
`;

const ThumbnailWrapper = styled.div`
	cursor: pointer;
	display: flex;
	flex-wrap: wrap;
	gap: 12px;
	@media ${Devices.tablet} {
	}
	@media ${Devices.mobile} {
		gap: 3px;
	}
`;

const Thumbnail = styled.img`
	border: ${(props) => props.border};
	width: 11.9rem;
	height: 11.9rem;
	border-radius: 4px;
	object-fit: contain;
	&:hover {
		border: 3px solid #6f7378;
	}
	@media ${Devices.tablet} {
		width: 9.2rem;
		height: 9.2rem;
	}
	@media ${Devices.mobile} {
		width: 5.8rem;
		height: 5.8rem;
	}
`;

const Main = styled.img`
	border: 3px solid #6f7378;
	border-radius: 4px;
	width: auto;
	padding: 0.5rem;
	max-height: 36rem;
	object-fit: contain;
`;

const Button = styled.button`
	width: 15rem;
	height: 5rem;
	border: 1px solid grey;
	border-radius: 4px;
	display: flex;
	justify-content: center;
	gap: 0.5rem;
	align-items: center;
	transition: all 0.15s ease;
	font-size: 1.2rem;
	&:hover {
		background-color: #cacaca;
	}
	@media ${Devices.tablet} {
		width: 12rem;
		height: 4rem;
		font-size: 0.9rem;
		img {
			width: 0.7rem;
		}
	}
	@media ${Devices.mobile} {
		width: 10rem;
		font-size: 0.8rem;
	}
`;

const PhotoWrapper = styled.div`
	display: flex;
	flex-direction: column;
	gap: 0.5rem;
	width: 50%;
	@media ${Devices.tablet} {
		width: 100%;
	}
	@media ${Devices.mobile} {
		gap: 3px;
	}
`;

const Icon = styled.div`
	display: flex;
	flex-direction: row;
	height: 3.75rem;
	align-items: center;
	z-index: 2;
	
	img {
		width: 3rem;
		@media ${Devices.tablet} {
		width: 2.5rem;
	}
	@media ${Devices.mobile} {
		width: 2rem;
	}
	}
	
`;
