import AngleDown from "../assets/SVG/angle-down-solid.svg";
import PlusSign from "../assets/SVG/plus-solid.svg";
import MinusSign from "../assets/SVG/minus-solid.svg";
import styled, { keyframes } from "styled-components";
import { useEffect, useState } from "react";
import Devices from "./Devices";
import ProjectsList from "./ProjectsList";

const Navbar = () => {
	const [prevScrollPos, setPrevScrollPos] = useState(null);
	const [scrollDir, setScrollDir] = useState(null);
	const [menuOpen, setMenuOpen] = useState(false);
	const [expandProjects, setExpandProjects] = useState(false);

	const handleScroll = (id, event) => {
		const section = `section-${id}`;
		let yOffset = 0;
		if (id === "skills") {
			yOffset = -45;
		} else {
			yOffset = -25;
		}

		const el = document.getElementById(section);
		setMenuOpen(false);
		const y = el.getBoundingClientRect().top + window.pageYOffset + yOffset;
		window.scrollTo({ top: y, behavior: "smooth" });
	};

	const handleExpandProjects = (event) => {
		event.stopPropagation();
		setExpandProjects((prevState) => !prevState);
	};

	const getScrollDirection = () => {
		setMenuOpen(false);
		if (window.matchMedia("(max-width:820px)").matches) {
			setScrollDir("up");
		} else {
			setScrollDir(window.scrollY > prevScrollPos ? "down" : "up");
			setPrevScrollPos(window.scrollY);
		}
	};

	useEffect(() => {
		window.addEventListener("scroll", getScrollDirection);
		return () => {
			window.addEventListener("scroll", getScrollDirection);
		};
	});

	return (
		<>
			<BackgroundFilter menuOpen={menuOpen}></BackgroundFilter>
			<MenuWrapper translate={scrollDir === "up" || scrollDir === undefined ? "0" : "-100%"}>
			{window.matchMedia("(max-width:820px)").matches ? <Title onClick={() => handleScroll("landing")}>Jacob Buckingham Web Dev</Title> : null}
				<MobileMenu onClick={() => setMenuOpen((prevState) => !prevState)}>
					<Bar translate={menuOpen ? "9px" : "0px"} rotate={menuOpen ? "45deg" : "0deg"}></Bar>
					<Bar opacity={menuOpen ? "0" : ""}></Bar>
					<Bar translate={menuOpen ? "-9px" : "0px"} rotate={menuOpen ? "-45deg" : "0deg"}></Bar>
				</MobileMenu>
			</MenuWrapper>
			<NavbarWrapper translate={scrollDir === "up" || scrollDir === undefined ? "0" : "-100%"} shift={menuOpen ? "0" : "100%"} menuOpen={menuOpen}>
				<NavButtonWrapper>
					{window.matchMedia("(max-width:820px)").matches ? <NavButton></NavButton> : null}
					<NavButton
						onClick={(event) => {
							handleScroll("landing", event);
						}}
					>
						<span to='/home'>Home</span>
						{window.matchMedia("(min-width:821px)").matches ? <GrowingLine></GrowingLine> : null}
					</NavButton>
					<NavButton
						onClick={(event) => {
							handleScroll("skills", event);
						}}
					>
						<span to='/about-us'>Skills</span>
						{window.matchMedia("(min-width:821px)").matches ? <GrowingLine></GrowingLine> : null}
					</NavButton>

					<NavButton
						onClick={(event) => {
							handleScroll("projects", event);
						}}
					>
						<div className='flex items-center'>
							{window.matchMedia("(max-width:820px)").matches ? (
								<IconWrapper onClick={handleExpandProjects}>
									<PlusMinusIcon src={expandProjects ? MinusSign : PlusSign} />
								</IconWrapper>
							) : null}
							<span to='/our-portfolio'>Projects</span>
							{window.matchMedia("(min-width:821px)").matches ? <AngleIcon src={AngleDown} /> : null}
						</div>
						{window.matchMedia("(min-width:821px)").matches ? <GrowingLine></GrowingLine> : null}
						{/*<DropDownWrapper></DropDownWrapper>*/}
					</NavButton>

					<DropDownMobile open={expandProjects}>
						{Object.values(ProjectsList).map((project) => (
							<NavButton
							key={project.id}
								open={expandProjects}
								onClick={(event) => {
									handleScroll(project.id, event);
									setExpandProjects(false);
								}}
							>
								<span>{project.name}</span>
							</NavButton>
						))}
					</DropDownMobile>

					<NavButton
						onClick={(event) => {
							handleScroll("about", event);
						}}
					>
						<span to='/vacancies'>About</span>
						{window.matchMedia("(min-width:821px)").matches ? <GrowingLine></GrowingLine> : null}
					</NavButton>
					<NavButton
						onClick={(event) => {
							handleScroll("contact", event);
						}}
					>
						<span to='/management'>Contact</span>
						{window.matchMedia("(min-width:821px)").matches ? <GrowingLine></GrowingLine> : null}
					</NavButton>
				</NavButtonWrapper>
			</NavbarWrapper>
		</>
	);
};

export default Navbar;
const DropDownWrapper = styled.div`
	position: absolute;
	width: 230px;
	height: 200px;
	background-color: grey;
	top: 131%;
	left: 0;
	bottom: 0;
	right: 0;
`;

const NavButton = styled.div`
	display: flex;
	flex-direction: column;
	flex-grow: 1;
	height: 100%;
	width: 100%;
	cursor: pointer;
	justify-content: center;
	align-items: center;
	transition: all 0.3s ease-in-out;
	transform: scaleY(1);
	position: relative;
	@media ${Devices.tablet} {
		transform: translateY(0);
		height: 80px;
	}
	@media ${Devices.mobile} {
		height: 60px;
	}
`;

const DropDownMobile = styled.div`
	display: none;
	@media ${Devices.tablet} {
		display: block;
		width: 80%;
		border-left: 1px solid grey;
		transition: all 0.3s ease-in-out;
		height: auto;
		opacity: ${(props) => (props.open ? "1" : "0")};
		${NavButton} {
			height: ${(props) => (props.open ? "50px" : "0")};
			span {
				font-size: 1rem;
				font-weight: 400;
			}
		}
	}
	@media ${Devices.mobile} {
		opacity: ${(props) => (props.open ? "1" : "0")};
		${NavButton} {
			height: ${(props) => (props.open ? "40px" : "0")};

			span {
				font-size: 0.9rem;
			}
		}
	}
`;

const BackgroundFilter = styled.div`
	position: fixed;
	width: 100%;
	height: 100vh;
	background-color: grey;
	z-index: 12;
	visibility: ${(props) => (props.menuOpen ? "visible" : "hidden")};
	opacity: ${(props) => (props.menuOpen ? "0.8" : "0")};
	transition: all 0.3s linear;
`;

const Title = styled.h4`
	@media ${Devices.tablet} {
		font-size: 1.2rem;
		font-weight: 400;
		display: flex;
		align-items: center;
		margin-left: 1rem;
	}
	@media ${Devices.mobile} {
		font-size: 1.1rem;
	}
`;

const MenuWrapper = styled.div`
	@media ${Devices.tablet} {
		height: 80px;
		width: 100%;
		display: flex;
		justify-content: space-between;
		position: sticky;
		top: 0;
		z-index: 13;
		background-color: white;
		box-shadow: 0 3px 10px -4px rgb(51 62 73 / 18%), 0 6px 17px 2px rgb(51 62 73 / 8%);
		transition: all 0.3s ease-in-out;
		-webkit-transition: transform 0.3s ease-in-out;
		-webkit-transition: -webkit-transform 0.3s ease-in-out;
		transform: translateY(${(props) => props.translate});
	}
	@media ${Devices.mobile} {
		height: 60px;
	}
`;

const Bar = styled.div`
	@media ${Devices.tablet} {
		display: flex;
		width: 25px;
		height: 3px;
		margin: 3px auto;
		-webkit-transition: all 0.3s ease-in-out;
		transition: all 0.3s ease-in-out;
		background-color: #636d77;
		transform: translateY(${(props) => props.translate}) rotate(${(props) => props.rotate});
		opacity: ${(props) => props.opacity};
	}
`;

const MobileMenu = styled.div`
	@media ${Devices.tablet} {
		display: flex;
		flex-direction: column;
		justify-content: center;
		margin-right: 2rem;
	}
`;

const NavbarWrapper = styled.div`
	display: flex;
	justify-content: flex-end;
	width: 100%;
	height: 10rem;
	align-items: center;
	position: fixed;
	top: 0%;
	z-index: 12;
	background-color: white;
	border-bottom: 1px solid grey;
	transition: all 0.3s ease-in-out;
	transform: translateY(${(props) => props.translate});
	@media ${Devices.tablet} {
		position: fixed;
		background-color: unset;
		justify-content: start;
		align-items: flex-end;
		margin-left: auto;
		display: flex;
		flex-direction: column;
		height: 100vh;
		width: 100%;
		transform: translateX(${(props) => props.shift});
		visibility: ${(props) => (props.menuOpen ? "visible" : "hidden")};
		margin-left: auto;
	}
`;

const IconWrapper = styled.div`
	width: 30px;
	height: 25px;
	display: flex;
	justify-content: center;
`;
const PlusMinusIcon = styled.img`
	width: 10px;
`;
const AngleIcon = styled.img`
	width: 10px;
	transition: all 0.15s ease-in-out;
	margin-left: 8px;
	${NavButton}:hover & {
		transform: scaleY(-1);
	}
`;

const GrowingLine = styled.div`
	border: 1px solid white;
	transition: all 0.15s ease-in-out;
	width: 0px;
	${NavButton}:hover & {
		width: 35%;
		border: 1px solid grey;
	}
`;

const NavButtonWrapper = styled.div`
	display: flex;
	justify-content: space-between;
	height: 100%;
	align-items: center;
	width: 60%;
	border-radius: 4px;
	@media ${Devices.tablet} {
		height: auto;
		width: 60%;
		transform: translateY(0);
		background-color: white;
		flex-direction: column;
		border-bottom: 1px solid grey;
		border-left: 1px solid grey;
	}
	@media ${Devices.mobile} {
		height: auto;
		width: 80%;
	}
`;
