import LanguageList from "./LanguageList";
import styled from "styled-components";
import ProjectsList from "./ProjectsList";
import { useRef, useState } from "react";
import { Link } from "react-router-dom";
import ArrowRight from "../assets/SVG/arrow-right-long-solid.svg";
import AngleDown from "../assets/SVG/angle-down-solid.svg";
import ToolTipIcon from "../assets/SVG/circle-question-regular.svg";
import Helper from "../assets/SVG/Animation.gif";
import Devices from "./Devices";
import { useEffect } from "react";

const Skills = () => {
	const [isVisible, setIsVisible] = useState({
		hireit: { shadow: "0 2px 5px rgb(51 62 73 / 10%)" },
		webscraper: { shadow: "0 2px 5px rgb(51 62 73 / 10%)" },
		tarkovtips: { shadow: "0 2px 5px rgb(51 62 73 / 10%)" },
		realestate: { shadow: "0 2px 5px rgb(51 62 73 / 10%)" },
		portfolio: { shadow: "0 2px 5px rgb(51 62 73 / 10%)" },
	});
	const [activeIcon, setActiveIcon] = useState();
	const [showHelper, setShowHelper] = useState(true);
	const [screenWidth, setScreenWidth] = useState();

	useEffect(() => {
		setScreenWidth(window.screen.availWidth);
	});

	const handleScroll = (id) => {
		const section = `section-${id}`;
		const el = document.getElementById(section);
		const yOffset = -25;
		const y = el.getBoundingClientRect().top + window.pageYOffset + yOffset;
		window.scrollTo({ top: y, behavior: "smooth" });
	};

	const handleClick = async (event) => {
		setActiveIcon(event.target.id);
		const matchedProjects = Object.values(ProjectsList).filter((project) => {
			return project.toolsUsed.some((tool) => {
				return tool.id === event.target.id;
			});
		});

		const matchedProjectIds = matchedProjects.map((project) => {
			return project.id;
		});

		Object.values(ProjectsList).forEach((project) => {
			setIsVisible((prevState) => {
				return { ...prevState, [project.id]: { ["shadow"]: "0 2px 5px rgb(51 62 73 / 10%)" } };
			});
		});

		matchedProjectIds.forEach((project) => {
			setIsVisible((prevState) => {
				return { ...prevState, [project]: { ["shadow"]: "0 3px 10px rgb(51 62 73 / 18%), 0 6px 17px rgb(51 62 73 / 18%)" } };
			});
		});
	};

	const GetIcons = ({ startIndex, endIndex }) => {
		return Object.values(LanguageList).map((icon, index) => {
			if (index >= startIndex && index <= endIndex) {
				return (
					<Icon
						key={index}
						onClick={handleClick}
						shadow={icon.id === activeIcon ? "0 3px 10px rgb(51 62 73 / 18%), 0 6px 17px rgb(51 62 73 / 18%)" : "0 2px 5px rgb(51 62 73 / 10%)"}
					>
						<img src={icon.src} id={icon.id} />
					</Icon>
				);
			}
		});
	};

	return (
		<MainWrapper id='section-skills'>
			<SubWrapper>
				<TitleWrapper>
					<SectionHeading>Skills</SectionHeading>
					<ToolTipWrapper>
						<ToolTip src={ToolTipIcon} onClick={() => setShowHelper((prevState) => !prevState)}></ToolTip>
						<HelperCard showHelper={showHelper} width={`${screenWidth - screenWidth / 10}px`}>
							<h4>Click on an icon to see where each tool has been used</h4>
							<Button onClick={() => setShowHelper((prevState) => !prevState)}>Get started</Button>
							<HelperGif src={Helper}></HelperGif>
						</HelperCard>
					</ToolTipWrapper>
				</TitleWrapper>

				<SubSubWrapper>
					{LanguageList && (
						<IconWrapper showHelper={showHelper}>
							<IconSubCont>
								<GetIcons startIndex={0} endIndex={2} />
							</IconSubCont>
							<IconSubCont>
								<GetIcons startIndex={3} endIndex={6} />
							</IconSubCont>
							<IconSubCont>
								<GetIcons startIndex={7} endIndex={11} />
							</IconSubCont>
							<IconSubCont>
								<GetIcons startIndex={12} endIndex={15} />
							</IconSubCont>
							<IconSubCont>
								<GetIcons startIndex={16} endIndex={21} />
							</IconSubCont>
						</IconWrapper>
					)}
					{ProjectsList && (
						<ProjectCardWrapper>
							<ProjectCardSubWrapper>
								{Object.values(ProjectsList).map((project, index) => (
									<>
										{index === 0 ? (
											<ProjectCard key={index} id={project.id} shadow={isVisible[project.id].shadow} onClick={() => handleScroll(project.id)}>
												<h4>{project.name}</h4>
												{window.matchMedia("(min-width:420px)").matches ? <p>{project.description}</p> : <p>{project.shortDescription}</p>}
											</ProjectCard>
										) : null}
									</>
								))}
							</ProjectCardSubWrapper>
							<ProjectCardSubWrapper>
								{Object.values(ProjectsList).map((project, index) => (
									<>
										{index >= 1 && index <= 2 ? (
											<>
												<ProjectCard key={index} id={project.id} shadow={isVisible[project.id].shadow} onClick={() => handleScroll(project.id)}>
													<h4>{project.name}</h4>
													{window.matchMedia("(min-width:420px)").matches ? <p>{project.description}</p> : <p>{project.shortDescription}</p>}
												</ProjectCard>
											</>
										) : null}
									</>
								))}
							</ProjectCardSubWrapper>
							<ProjectCardSubWrapper>
								{Object.values(ProjectsList).map((project, index) => (
									<>
										{index >= 3 && index <= 5 ? (
											<ProjectCard key={index} id={project.id} shadow={isVisible[project.id].shadow} onClick={() => handleScroll(project.id)}>
												<h4>{project.name}</h4>
												{window.matchMedia("(min-width:420px)").matches ? <p>{project.description}</p> : <p>{project.shortDescription}</p>}
											</ProjectCard>
										) : null}
									</>
								))}
							</ProjectCardSubWrapper>
						</ProjectCardWrapper>
					)}
				</SubSubWrapper>
			</SubWrapper>
		</MainWrapper>
	);
};

export default Skills;

const MainWrapper = styled.div`
	display: flex;
	flex-direction: column;
	background-color: #f9f9f9;
	padding: 2rem 0;
	@media ${Devices.tablet} {
		padding: 1.5rem 0;
	}
	@media ${Devices.mobile} {
		padding: 1rem 0;
	}
`;

const SubWrapper = styled.div`
	display: flex;
	flex-direction: column;
	width: 2000px;
	margin: 0 auto;
	@media ${Devices.tablet} {
		flex-direction: column;
		width: 100%;
		padding: 0 1.5rem;
	}
	@media ${Devices.mobile} {
		padding: 0 1rem;
	}
`;
const SubSubWrapper = styled.div`
	display: flex;
	flex-direction: row;
	justify-content: center;
	position: relative;
	gap: 2rem;
	@media ${Devices.tablet} {
		flex-direction: column;
		gap: 1.5rem;
	}
	@media ${Devices.mobile} {
		gap: 1rem;
	}
`;

const TitleWrapper = styled.div`
	display: flex;
`;
const ToolTipWrapper = styled.div`
	margin-left: 0.5rem;
	position: relative;
`;

const ToolTip = styled.img`
	width: 25px;
	cursor: pointer;
	@media ${Devices.tablet} {
		width: 15px;
	}
	@media ${Devices.mobile} {
	}
`;

const HelperGif = styled.img`
	height: auto;
	border: 3px solid #6f7378;
	border-radius: 4px;
`;

const HelperCard = styled.div`
	width: 1400px;
	height: auto;
	box-shadow: 0 3px 10px rgb(51 62 73 / 18%), 0 6px 17px rgb(51 62 73 / 18%);
	transform: translateY(${(props) => (props.showHelper ? "0" : "-50%")}) translateX(${(props) => (props.showHelper ? "15%" : "-50%")})
		scale(${(props) => (props.showHelper ? "1" : "0.0")});
	transition: all 0.5s ease-in;
	background: white;
	position: absolute;
	padding: 1rem;
	border-radius: 4px;
	display: flex;
	flex-direction: column;
	gap: 0.5rem;
	z-index: 10;
	h4 {
		font-size: 3.5rem;
		font-weight: 600;
	}
	@media ${Devices.tablet} {
		width: ${(props) => props.width};
		height: auto;
		transform: translateY(${(props) => (props.showHelper ? "10%" : "-50%")}) translateX(${(props) => (props.showHelper ? "-10%" : "-50%")})
			scale(${(props) => (props.showHelper ? "1" : "0.0")});
		h4 {
			font-size: 2rem;
			font-weight: 600;
		}
	}
	@media ${Devices.mobile} {
		width: ${(props) => props.width};

		transform: translateY(${(props) => (props.showHelper ? "10%" : "-50%")}) translateX(${(props) => (props.showHelper ? "-20%" : "-50%")})
			scale(${(props) => (props.showHelper ? "1" : "0.0")});
		h4 {
			font-size: 1.6rem;
			font-weight: 600;
		}
	}
`;

const IconWrapper = styled.div`
	display: flex;
	gap: 1rem;
	@media ${Devices.tablet} {
		justify-content: center;
	}
	@media ${Devices.mobile} {
	}
`;

const Button = styled.button`
	width: 15rem;
	height: 5rem;
	border: 1px solid #6f7378;
	background-color: #6f7378;
	color: white;
	font-weight: 600;
	font-size: 1.5rem;
	border-radius: 4px;
	transition: all 0.15s ease;
	font-size: 1.4rem;
	&:hover {
		background-color: #cacaca;
	}
	@media ${Devices.tablet} {
		width: 12rem;
		height: 4rem;
		font-size: 1.1rem;
		img {
			width: 0.7rem;
		}
	}
	@media ${Devices.mobile} {
		width: 10rem;
		font-size: 1rem;
	}
`;
const SectionHeading = styled.h2`
	border-bottom: 1px solid grey;
	margin-bottom: 2rem;
	@media ${Devices.tablet} {
		margin-bottom: 1.5rem;
	}
	@media ${Devices.mobile} {
		margin-bottom: 1rem;
	}
`;

const ProjectCardWrapper = styled.div`
	display: flex;
	flex-direction: column;
	justify-content: center;
	gap: 1.5rem;
	margin-left: 2rem;
	@media ${Devices.tablet} {
		margin-left: 0;
		gap: 1.5rem;
	}
	@media ${Devices.mobile} {
		gap: 1rem;
		h4 {
			font-size: 1rem;
			font-weight: 600;
		}
	}
`;
const ProjectCardSubWrapper = styled.div`
	display: contents;
	@media ${Devices.tablet} {
		display: flex;
		gap: 1.5rem;
	}
	@media ${Devices.mobile} {
		gap: 1rem;
	}
`;

const ProjectCard = styled.div`
	width: 56rem;
	height: 14rem;
	box-shadow: ${(props) => props.shadow};
	background: white;
	padding: 1rem;
	border-radius: 4px;
	cursor: pointer;
	transition: box-shadow 0.15s ease;
	&:hover {
		box-shadow: 0 3px 10px rgb(51 62 73 / 18%), 0 6px 17px rgb(51 62 73 / 18%);
	}
	@media ${Devices.tablet} {
		width: ${(props) => (props.id === "hireit" ? "50%" : "100%")};
		margin: 0 auto;
		height: 9.5rem;
		padding: 0.5rem;
		p {
			font-size: 0.8rem;
		}
	}
	@media ${Devices.mobile} {
		height: 8rem;
		padding: 0.5rem;
	}
`;

const Icon = styled.div`
	display: flex;
	align-items: center;
	z-index: 2;
	padding: 0.5rem;
	border-radius: 4px;
	background-color: white;
	transition: box-shadow 0.15s ease;
	box-shadow: ${(props) => props.shadow};
	&:hover {
		cursor: pointer;
		box-shadow: 0 3px 10px rgb(51 62 73 / 18%), 0 6px 17px rgb(51 62 73 / 18%);
	}
	img {
		width: 6rem;
		height: 6rem;
	}
	@media ${Devices.tablet} {
		padding: 0.25rem;
		img {
			width: 3.5rem;
			height: 3.5rem;
		}
	}
	@media ${Devices.mobile} {
		img {
			width: 2.5rem;
			height: 2.5rem;
		}
	}
`;

const IconSubCont = styled.div`
	display: flex;
	flex-direction: column;
	justify-content: center;
	gap: 1rem;
	@media ${Devices.tablet} {
		gap: 0.5rem;
	}
	@media ${Devices.mobile} {
		gap: 0.1rem;
	}
`;
