import styled from "styled-components";
import Devices from "./Devices";
import Github from "../assets/SVG/github-original-wordmark.svg";
import Linkedin from "../assets/SVG/linkedin-in-brands.svg";

const Footer = () => {
	return (
		<FooterWrapper>
			<h4>Made by Jacob Buckingham</h4>
            <ImgWrapper>
			<a href='https://github.com/jbuck44' target='_blank'>
				<Icon src={Github} />
			</a>
			<a href='https://www.linkedin.com/in/jacob-buckingham-471603202' target='_blank'>
				<Icon src={Linkedin} />
			</a></ImgWrapper>
		</FooterWrapper>
	);
};

export default Footer;

const FooterWrapper = styled.div`
	height: 300px;

	display: flex;
	justify-content: center;
	align-items: center;
    gap: 2rem;
    background-color: #f9f9f9;
	@media ${Devices.tablet} {
		height: 150px;    gap: 1.5rem;
        h4 {
            font-size: 1.2rem;
        }
	}
	@media ${Devices.mobile} {
        height: 120px;	gap:1rem;
        h4 {
            font-size: 1rem;
        }
	}
`;
const ImgWrapper = styled.div`
display: flex;
gap: 2rem;
@media ${Devices.tablet} {
    gap: 1.5rem;
	}
	@media ${Devices.mobile} {
		gap:1rem;
	}
`

const Icon = styled.img`
	width: 3rem;
	@media ${Devices.tablet} {
		width: 2rem;
	}
	@media ${Devices.mobile} {
		width: 1.5rem;
	}
`;
