import styled from "styled-components";
import AngleDown from "../assets/SVG/angle-down-solid.svg";
import Devices from "./Devices";
import Me from "../assets/me.jpg";
import Github from "../assets/SVG/github-original-wordmark.svg";
import Linkedin from "../assets/SVG/linkedin-in-brands.svg";

const LandingPage = () => {	

	return (
		<MainWrapper id='section-landing'>
			<SubWrapper>
				<TitleBox>
					<h1>Jacob Buckingham</h1>
					<h4>Aspiring Frontend Developer</h4>
					{window.matchMedia("(min-width:821px)").matches ? (
						<ImageSubWrapper margin={"2rem auto"} direction={"row"}>
							<a href='https://github.com/jbuck44' target='_blank'>
								<Icon src={Github} />
							</a>
							<a href='https://www.linkedin.com/in/jacob-buckingham-471603202' target='_blank'>
								<Icon src={Linkedin} />
							</a>
						</ImageSubWrapper>
					) : null}
				</TitleBox>

				<ImageWrapper>
					<ImageSubWrapper>
						<Image src={Me} />
					</ImageSubWrapper>
					{window.matchMedia("(max-width:820px)").matches ? (
						<ImageSubWrapper  direction={"column"}>
							<a href='https://github.com/jbuck44' target='_blank'>
								<Icon src={Github} />
							</a>
							<a href='https://www.linkedin.com/in/jacob-buckingham-471603202' target='_blank'>
								<Icon src={Linkedin} />
							</a>
						</ImageSubWrapper>
					) : null}
				</ImageWrapper>
			</SubWrapper>
		</MainWrapper>
	);
};

export default LandingPage;

const MainWrapper = styled.div`
	height: 100vh;
	display: flex;
	justify-content: center;
	h4{
		text-decoration: none;
	}
	@media ${Devices.tablet} {
		height: 92vh;
	}
	@media ${Devices.mobile} {
		height: 90vh;
	}
`;

const SubWrapper = styled.div`
	display: flex;
	gap: 8rem;
	@media ${Devices.tablet} {
		flex-direction: column;
		gap: 2.5rem;
		justify-content: flex-start;
		margin-top: 17%;
		padding: 0 0.5rem;
	}
	@media ${Devices.mobile} {
		padding: 0 2.5rem;
	}
`;

const TitleBox = styled.div`
	display: flex;
	flex-direction: column;
	justify-content: center;
`;

const ImageSubWrapper = styled.div`
	display: flex;
	flex-direction: ${(props) => props.direction};
	margin: ${(props) => props.margin};
	gap: 2rem;
`;

const Icon = styled.img`
	width: 5rem;
	@media ${Devices.tablet} {
		width: 4.5rem;
	}
	@media ${Devices.mobile} {
		width: 3rem;
	}
`;

const Image = styled.img`
	width: 30rem;
	border-radius: 4px;
	object-fit: contain;
	@media ${Devices.tablet} {
		width: 20rem;
	}
	@media ${Devices.mobile} {
		width: 15rem;
	}
`;

const ImageWrapper = styled.div`
	display: flex;
	align-items: center;
	justify-content: space-between;
	
`;
